.logo { /* Safari */
  display: flex; /* Safari 7.0+ */
  align-items: center;
  justify-content: center;
}

body {
  margin: 0
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}
